@import url("https://fonts.googleapis.com/css2?family=Bad+Script&family=Patrick+Hand&display=swap");

/* ToolBar Styling */
.navBar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 150px;
  padding: 10px 90px;
  box-sizing: border-box;
  background: transparent;
  transition: 0.4s;
  font-family: "Patrick Hand", cursive;
  background-color: #F4EFDB;
}

.navLogo {
  height: 100px;
  float: left;
  margin-left: 0%;
  margin-top: 2%;
}

nav ul {
  list-style: none;
  margin-top: 2%;
  margin-right: 0%;
  padding: 0;
  display: flex;
  float: right;
}

nav ul li a {
  line-height: 100px;
  padding: 12px 30px;
  text-decoration: none;
  font-size: 15px;
  font-weight: bold;
}

nav ul li a:hover {
  color: #D31D0F;
}

.navButton {
  color: #EA4C29;
  margin: 3px;
}

.walletButton {
  color: white;
  background-color: #EA4C29;
  border-radius: 10px;
  box-shadow: 7px 6px 28px 2px rgba(0, 0, 0, 0.24);
  margin: 5px;
}

.walletButton:active {
  transform: scale(0.98);
  /* Scaling button to 0.98 to its original size */
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  /* Lowering the shadow */
}

/* ToolBar Styling end */